.alert {
  color: #05090b;
  background-color: #afccdf;
  border: 0 solid #679dc2;
  border-radius: 0;
  margin-bottom: 1.5em;
  padding: .375em 0;
  font-size: 1.4rem;
}

.alert > h4 {
  color: #05090b;
  margin: 0;
}

.alert--success {
  color: #000;
  background-color: #5eb337;
  border-color: #4a8c2b;
}

.alert--success > h4 {
  color: #000;
}

.alert--error {
  color: #fceeee;
  background-color: #9c1c1e;
  border-color: #711416;
}

.alert--error > h4 {
  color: #fceeee;
}

.alert--warning {
  color: #716209;
  background-color: #f7ea9c;
  border-color: #efd63e;
}

.alert--warning > h4 {
  color: #716209;
}

.alert--info {
  color: #294e67;
  background-color: #afccdf;
  border-color: #8bb5d1;
}

.alert--info > h4 {
  color: #294e67;
}

.alert--block {
  padding-top: 14px;
  padding-bottom: 14px;
}

.alert--block > p, .alert--block > ul {
  margin-bottom: 0;
}

.alert--block > p + p {
  margin-top: 5px;
}

.media, .media-body {
  zoom: 1;
  overflow: hidden;
}

.media, .media .media {
  margin-top: 15px;
}

.media:first-child {
  margin-top: 0;
}

.media-object {
  display: block;
}

.media-heading {
  margin: 0 0 5px;
}

.media .pull-left {
  float: left;
  margin-right: 12px;
}

.media .pull-right {
  float: right;
  margin-left: 12px;
}

.media-list {
  margin-left: 0;
  list-style-type: none;
}

.modal-backdrop {
  z-index: 1040;
  background-color: #000;
  position: fixed;
  inset: 0;
}

.modal-backdrop.fade {
  opacity: .4;
